<template>
	<view :data-theme="appTheme">
		<movable-area style="width: 100%;height: 100%;">
		<u-navbar :is-back="false" :background="background">
			<view class="slot-wrap">
				<view class="leftTitle">
					<text @click="getWeather">{{addressName}}</text>
				</view>
				<text class="iconfont iconxiaoxi" @click="notice"></text>
			</view>
		</u-navbar>
		<!-- #ifdef MP-WEIXIN -->
		<!-- 公众号的关注，只适用于扫小程序码时展示 -->
		<official-account></official-account>
		<!-- #endif -->
		<!-- <u-button @click="switchLang">切换语言</u-button> -->
		<!-- banner -->
		<!-- <button type="default" @click="changeTheme({appbg:'AppBlack'})">更改颜色</button> -->
		<view class="banner">
			<u-swiper :list="bannerList" name="image" height='390' img-mode='aspectFit' bg-color='#ffffff'></u-swiper>
		</view>
		<view class="Index">
			<!-- index-menu -->
			<view :animation="animationData" class="indexMenu">
				<index-menu></Index-menu>
			</view>
			<!-- 病历管理 -->
			<module-title :title="$t('index.caseManage')" index='0'></module-title>
			<view class="casecontent">
				<view class="segmentedMenu casemenu">
					<uni-segmented-control :current="casecurrent" :values="items" @clickItem="menuClick" style-type="text" active-color="#86B0D4"></uni-segmented-control>
				</view>
				<view v-if="token">
					<scroll-view class="contentlist" scroll-x="true" v-if="caselist.length==0?false:true">
						<IndexCaselist v-for="(item,index) in caselist" :key='index' :casecode="item.caseHistoryNo" :Uname='item.name' :Caseid='item.caseHistoryId' :imgUrl='item.previewImage'></IndexCaselist>
					</scroll-view>
					<scroll-view class="contentlist" scroll-x="true" v-else>
						<u-tag text="暂无数据" mode="light" color="#ffffff" border-color='#86B0D4' bg-color='#86B0D4'/>
					</scroll-view>
				</view>
				<view class="loginStyle" v-else>
					<u-tag @click="login" text="点击登录,查看更多数据" mode="light" color="#ffffff" border-color='#86B0D4' bg-color='#86B0D4'/>
				</view>
			</view>		
			<!-- 设备管理 -->
			<view class="deviceData">
				<view class="deviceTitle">
					<text>{{$t('index.deviceManage')}}</text>
					<text @click="more()">{{$t('index.Moremenu')}}</text>
				</view>
				<view class="deviceWeixin" v-if="token">
					<u-read-more v-if="deviceData.length>=2" ref="uReadMore" text-indent=0 :toggle="true" :show-height="360" :shadow-style="shadowStyle" color='#000000' close-text='' open-text=''>
						<GroupsList class="deviceList" v-for="(item,index) in deviceData" :key='index' :devices='item.devices' :Uname='item.name' :Deviceid='item.id'></GroupsList>
					</u-read-more>
					<view v-else>
						<GroupsList class="deviceList" v-for="(item,index) in deviceData" :key='index' :devices='item.devices' :Uname='item.name' :Deviceid='item.id'></GroupsList>
					</view>
				</view>
				<view class="devicelogin" style="" v-else>
					<u-tag @click="login" text="点击登录,查看更多数据" mode="light" color="#ffffff" border-color='#86B0D4' bg-color='#86B0D4'/>
				</view>
			</view>
			<!-- 手术方法库 -->
			<module-title :title="$t('index.Surgical')" index='1'></module-title>
			<view class="segmentedMenu surgical">
				<uni-segmented-control :current="current" :values="surgicalitems" @clickItem="onClickItem" style-type="text" active-color="#86B0D4"></uni-segmented-control>
			</view>
			<view class="classes" v-if="token">
				<surgicalList style='width: 100%;' v-for='(item,index) in surgicalListAll' :key='index' :Surgicalid="item.surgicalPlanId" :docName='item.doctorName' :time='item.createTime' :imgUrl='item.previewList[0].fileDownloadUrl' :content='item.title' :content_title="item.surgicalPlanName" :doctor="item.doctor"></surgicalList>
			</view>
			<view class="devicelogin" v-else>
				<u-tag @click="login" text="点击登录,查看更多数据" mode="light" color="#ffffff" border-color='#86B0D4' bg-color='#86B0D4'/>
			</view>
			<view class="StrenchMore" v-if="token&&surgicalListAll.length!=0" @click="StrenchMore()" hover-class="bgColor">
				<uni-icons :type="typeicon" size='20'></uni-icons>
			</view>
			<!-- 公开模型 开发中-->
			<module-title :title="$t('index.PublicModel')" index='2'></module-title>
			<view class="publicmodel">
				<classifyModel style='width: 100%;' :publicData='publicData'></classifyModel>
			</view>
			<view class="checkAllModel" @click="AllPublicModel">
				<text>查看全部公开模型</text>
			</view>
			<!-- 树蚁商城 开发中-->
			<!-- <view>
				<module-title title="树蚁商城" index='3'></module-title>
				<store :shoyiiStore='shoyiiStore'></store>
			</view> -->
		</view>
		<!-- 聊天入口 -->
		<view class="chat" v-if="token" style="position: fixed;bottom: 20%;right: 20%;">
			<movable-view direction='all' style="opacity: .8;">
				<view @click="Enterchat" class="chatIcon" style="position: relative;display: flex;align-items: center;justify-content: center;width: 50px;height: 50px;border-radius: 50%;background-color: #007AFF;">
					<u-badge :is-dot="true" type="error" v-if="isDot"></u-badge>
					<u-icon color="#fff" size="48" name="chat"></u-icon>
				</view>
			</movable-view>
		</view>
		
		</movable-area>
		<!-- 网络错误 -->
		<u-no-network></u-no-network>
	</view>
</template>
<script>
	import {getBanner} from '@/utill/api/Index/banner.js'
	import {getSurgical,getSurgicalparam} from '@/utill/api/surgical/getSurgical.js' 
	import {getCaselist} from '@/utill/api/case/getCaselist.js' 
	import {querydevice} from '@/utill/api/DeviceGroup/querydevice.js'
	import {getPublicModel} from '@/utill/api/model/getModel.js'
	import {getInfor} from '@/utill/api/personalInfor/getInfor.js'
	import surgicalList from '@/components/surgical/surgicalList.vue'
	import modellist from '@/components/model/modellist.vue'
	import classifyModel from '@/components/model/classifyModel.vue'
	import IndexMenu from './components/IndexMenu.vue'
	import moduleTitle from './components/moduletitle.vue'
	import IndexCaselist from './components/IndexCaselist.vue'
	import store from './components/store.vue'
	import {mapGetters,mapMutations,mapState} from 'vuex' 
	// 引入高德地图
	import amap from '../../common/js/map/amap-wx.130.js'; 
	import {myRequest} from '@/utill/request.js'
	// 判断权限的引入
	import permision from "@/js_sdk/wa-permission/permission.js"
	
	var dbclickTimer=null;
	var animationTimer=null;
	var stopTimer=null;
	var amapPlugin=null;
	export default {
		components:{
			surgicalList,
			modellist,
			classifyModel,
			IndexMenu,
			moduleTitle,
			IndexCaselist,
			store
		},
		data() {
			return { 
				userId:uni.getStorageSync('doctorId'),
				background: {
					backgroundColor: '#ffffff',
				},
				publicData:[],
				MyModel:1,//用于区分我的模型库和公开模型库进入详情页的不同
				// banner
				bannerList:[],//轮播图
				shadowStyle: {
					backgroundImage: "none",
					paddingTop: "0",
					marginTop: "10rpx"
				},
				surgicalListAll:[],//全部的手术方法数据
				shoyiiStore:[//树蚁商店
					{url:'/static/image/model.png',title:'UNVEIL UNVEIL UNVEIL AND EXP',amount:'￥5,699'},
					{url:'/static/image/shoyiilogo.png',title:'UNVEIL AND EXP',amount:'￥5,699'},
					{url:'/static/image/wechat.png',title:'UNVEIL AND EXP',amount:'￥5,699'},
				],
				// 刷新动画
				animationData: {},
				// 病例menu
				items: ['正在编辑中','正在物流中','已完成病例'],
				// 手术方法库menu
				surgicalitems:['脊柱','上肢','下肢','骨盆'],
				current: 0,//默认手术方法库为脊柱
				// 病例相关数据
				casecurrent:0,
				caselist:[],//全部病例数据
				// 设备数据
				deviceData:[],
				token:'',//token，用于判断是否需要显示登陆btn
				size:2,//手术方法的每页数据
				total:'',//手术方法的总数
				typeicon:'arrowdown',
				// 这里填写高德开放平台上申请的key,小程序的key
				key:'34c1d7c06f414ab22e27351bf0c8ef53',
				addressName:'首页',
				isDot:false,
				tabClick:false,
				boolean:true,
			}
		},
		watch:{
			size(newValue,oldValue){
				if(newValue==4){
					this.typeicon='arrowup'
				}else{
					this.typeicon='arrowdown'
				}
			},
			'socketData': function(val) {
				if(val=='head'){//心跳回复
					return
				}
				this.isDot=true
				uni.showTabBarRedDot({ //红点
				   index:0,
				   complete(e) {
						console.log(e)
				   }
				})
			},
		},
		computed:{
			...mapGetters (['appTheme','DataStatus']),
			...mapState(['socketData','hasLogin'])
		},
		methods: {
			// 判断是否获取权限
			requestAndroidPermission(permisionID) {
			    var result = permision.requestAndroidPermission(permisionID)
				console.log(result)
			    var strStatus
			    if (result == 1) {
			        strStatus = "已获得授权"
			    } else if (result == 0) {
			        strStatus = "未获得授权"
			    } else {
			        strStatus = "被永久拒绝权限"
					// permision.gotoAppPermissionSetting()
			    }
			    uni.showModal({
			        content: permisionID + strStatus,
			        showCancel: false
			    });
			},
			getWeather(){
				// uni.navigateTo({ //高德小程序jdk
				// 	url:'/pages/map/map'
				// })
				// #ifdef APP-PLUS||MP-WEIXIN
					uni.navigateTo({ //高德小程序jdk
						url:'/pages/weather/weather'
					})
					// uni.navigateTo({
					// 	url:'/pages/contactUs/contactUs'
					// })
					// uni.navigateTo({ //高德小程序jdk
					// 	url:'/pages/errpage/errpage'
					// })
					// uni.navigateTo({//支付宝支付
					// 	url:'/pages/aboutUs/aboutUs'
					// })
				// #endif
				// uni.navigateTo({//list页面渲染
				// 	url:'/pages/NavTest/NavTest'
				// })
				// uni.navigateTo({//echarts图标渲染
				// 	url:'/pages/map/echarts'
				// })
			},
			getsdkLocation(){
				amapPlugin.getRegeo({  
					success: (data) => {  
						this.addressName = data[0].regeocodeData.addressComponent.district;   
					},
					 fail:(err)=>{
						 console.log(err)  
					 }
				});  
			},
			notice(){
				uni.navigateTo({
					url:'/pages/notice/notice'
				})
			},
			Enterchat(){
				uni.hideTabBarRedDot({
					index:0,
					complete(e) {
						console.log('红点消失')
					}
				})
				this.isDot=false
				uni.navigateTo({
					url:'/pages/shoyiiChat/index'
				})
			},
			...mapMutations(['changeTheme']),
			// 语言切换
			// switchLang() {
			// 	// 切换为英文
			// 	this.$i18n.locale = this.$i18n.locale === 'en' ? 'zh' : 'en';
			// 	uni.setTabBarItem({
			// 		index: 0,
			// 		text: this.$t('tabbar.tab1'),
			// 	})
			// 	uni.setTabBarItem({
			// 		index: 1,
			// 		text: this.$t('tabbar.tab2'),
			// 	})
			// 	uni.setTabBarItem({
			// 		index: 2,
			// 		text: this.$t('tabbar.tab3'),
			// 	})
			// 	uni.setTabBarItem({
			// 		index: 3,
			// 		text: this.$t('tabbar.tab4'),
			// 	})
			// 	uni.setTabBarItem({
			// 		index: 4,
			// 		text: this.$t('tabbar.tab5'),
			// 	})
			// },
			// 获取轮播图
			getBannerList(){
				getBanner({}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.bannerList=res.data.object
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 获取公开模型
			getPublicModel(){
				getPublicModel({}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.publicData=res.data.object.list.slice(0,1)
					}
				}).catch(err=>{
					uni.showToast({
						title:'err',
						icon:'none'
					})
				})
			},
			// 病例menu
			menuClick(e){
				if (this.casecurrent !== e.currentIndex) {
					this.casecurrent = e.currentIndex;
				}
				console.log(this.casecurrent)//默认是0 
				if(this.casecurrent==0){//正在编辑
					this.getCaseData("WFB")
				}else if(this.casecurrent==1){//物流中
					this.getCaseData('YFB')//先默认是已发布
				}else if(this.casecurrent==2){//已完成
					this.getCaseData('YWC')	
				}
			},
			login(){//登录页面
				uni.navigateTo({
					url:'/pages/login/tellogin?pages='+'/pages/index/index'+'&pagetype='+1,
					animationType:'pop-in'
				})
			},
			// 设备管理
			getdevice(){
				querydevice({}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.deviceData=res.data.object.slice(0,4)
						if(res.data.object.length>=2){
							this.$nextTick(() => {
								this.$refs.uReadMore.init();
							})
						}
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				})
			},
			surgicalMenu(){
				if(this.current==0){//脊柱
					this.getSurcialData(14)
				}else if(this.current==1){//上肢
					this.getSurcialData(15)
				}else if(this.current==2){//下肢
					this.getSurcialData(16)
				}else if(this.current==3){//骨盆
					this.getSurcialData(368)
				}
			},
			StrenchMore(){
				if(this.typeicon=='arrowdown'){//展开
					if(this.size<this.total&&this.size<4){
						this.size+=2
						console.log(this.size)
						this.surgicalMenu()
					}
				}else{//收回
					this.size=2
					this.surgicalMenu()
				}
			},
			// 更多menu
			more(){//设备管理
				uni.navigateTo({
					url:'/pages/deviceManage/deviceManage'
				})
			},
			// 手术方法库menu
			onClickItem(e){
				this.size=2
				this.typeicon='arrowdown'
				// 样式调整
				if (this.current !== e.currentIndex) {
					this.current = e.currentIndex;
				}
				this.surgicalMenu()
			},
			// 获取手术方法数据
			getSurcialData(id){
				console.log(id)
				// #ifdef APP-PLUS||H5
				var _this=this
				getSurgical({
					start:1,
					length:this.size,
					surgicalPlanTypeId:id
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.total=res.data.object.pages
						res.data.object.list.filter(item=>{
							if(!item.doctor){
								item.doctor.avatarLinks=uni.getStorageSync('uploadLogo')
							}
							if(!item.previewList[0]){
								item.previewList.push({
									fileDownloadUrl:uni.getStorageSync('uploadLogo')
								})
							}
						})
						this.surgicalListAll=res.data.object.list
						console.log(this.surgicalListAll)
					}else{
						uni.hideLoading()//关闭加载提示
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				})
				// #endif
				// #ifdef MP-WEIXIN
				var _this=this
				getSurgicalparam({
					start:1,
					length:this.size,
					surgicalPlanTypeId:id
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.total=res.data.object.total
						res.data.object.list.filter(item=>{
							if(!item.doctor){
								item.doctor.avatarLinks=uni.getStorageSync('uploadLogo')
							}
							if(!item.previewList[0]){
								item.previewList.push({
									fileDownloadUrl:uni.getStorageSync('uploadLogo')
								})
							}
						})
						this.surgicalListAll=res.data.object.list
						console.log(this.surgicalListAll)
					}else if(res.data.code==2){
						
					}else{
						uni.hideLoading()//关闭加载提示
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				})
				// #endif
			},
			// 获取病例数据
			getCaseData(status){
				console.log(status)
				getCaselist({
					status:status,
					identity:'ALL',
					type:'ALL',
					page:1,
					size:6,
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						res.data.object.list.filter(item=>{
							if(!item.previewImage){
								item.previewImage=uni.getStorageSync('uploadLogo')
							}
							if(!item.name||item.name==''){
								item.name='未填写'
							}
						})
						this.caselist=res.data.object.list
						console.log(this.caselist)
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				})
			},
			// 查询所有的模型数据
			AllPublicModel(){
				uni.navigateTo({
					url:'/pages/publicModel/publicModel'
				})
			},
			// 刷新页面的动画
			reflesh(){
				var animation = uni.createAnimation({
					duration: 1000,
					timingFunction: 'linear',
				})
				this.animation = animation
				animation.opacity(0).translateY(-100).step({ duration: 1000 })
				this.animationData = animation.export()
				animationTimer=setTimeout(function() {
				  animation.opacity(1).translateY(0).step()
				  this.animationData = animation.export()
				}.bind(this), 1000)
			 },
		},
		// 初始化加载数据
		onLoad(options) {
			console.log(this.$t('lang.intro'));
			// #ifdef APP-PLUS||MP-WEIXIN
			// 高德地图小程序sdk初始化加载
			amapPlugin = new amap.AMapWX({
				key:this.key  
			});
			this.getsdkLocation()
			// #endif
			// #ifndef MP-WEIXIN
			// 首页连接socket
			if(!this.userId){//先登录，才能创建
				return;
			}else if(!this.$store.state.is_open_socket){ //socket已关
				this.$store.dispatch('WEBSOCKET_INIT',this.userId)
			}
			// #endif
			console.log('onload')	
		},
		onShow() {
			this.token=uni.getStorageSync('token')
			console.log(this.DataStatus)
			// 切换账号，及退出登录时,进行数据的清空处理,新账号登录时,重新加载数据
			console.log(this.hasLogin)
			if(this.hasLogin){ //为true，则刷新数据
				this.surgicalListAll=[];
				this.caselist=[];
				this.deviceData=[]
			}
			if(uni.getStorageSync('token')){//token存在才获取接口
				getInfor({}).then(res=>{//进行token过期时间校验
					console.log(res)
					if(res.data.code==0){//表明token没有过期，可以调用接口
						uni.setStorageSync('auditstatus',res.data.object.auditStatus)
						if(this.surgicalListAll.length==0||this.DataStatus){//当手术方法的数据为0时，每次再加载数据，如果有数据，就不加载
							this.getSurcialData(14)//默认查询脊柱的数据
						}
						if(this.caselist.length==0||this.DataStatus){//默认是未发布的病例为0时，再加载数据
							this.getCaseData('WFB')//默认是未发布病例数据
						}
						if(this.deviceData.length==0||this.DataStatus){
							this.getdevice()
						}
					}else{
						// 这块清除缓存不要清除掉引导页的缓存
						 uni.removeStorageSync('doctorId');
						 uni.removeStorageSync('token');
						 uni.removeStorageSync('auditstatus');
						 uni.removeStorageSync('openid');
						uni.showModal({
							title:'登录信息已失效',
							content:"是否前往登录",
							confirmText:'登录',
							cancelText:'稍后登录',
							confirmColor:'#86B0D4',
							success(e){
								if (e.confirm) {
									uni.navigateTo({//进入登陆页面
										url:'/pages/login/tellogin'
									})
								} else if (e.cancel) {//取消登陆后，清除所有缓存
								}
							}
						})
					}
				})			
			} 
			// 这两个是不需要登录就展示的
			if(this.publicData.length==0||this.DataStatus){
				this.getPublicModel()
			}	
			if(this.bannerList.length==0||this.DataStatus){
				this.getBannerList()
			}
			// #ifdef MP-WEIXIN
			// 小程序退出后，再进入会有缓存，不会触发onload事件
			if(!this.userId){//先登录，才能创建
				return;
			}else if(!this.$store.state.is_open_socket){ //socket已关
				this.$store.dispatch('WEBSOCKET_INIT',this.userId)
			}
			// #endif
		},
		// 刷新加载数据
		onPullDownRefresh (){
			console.log('refresh');
			this.reflesh()//动画效果
			this.getCaseData('WFB')
			this.getSurcialData(14)
			this.getdevice()
			this.getBannerList()
			stopTimer=setTimeout(function () {
				uni.stopPullDownRefresh();//停止刷新，这边可以进行数据的更新
			}, 1000);
		},
		// 分享朋友
		// #ifdef MP-WEIXIN
		onShareAppMessage: function (res) {
		    if (res.from === 'button') {//定义了share 才是button
		      // 来自页面内转发按钮
			  // return {
				 //  title: '自定义btn分享',
				 //  path: '/pages/index/index'
			  // }
		    }else{//menu为原生胶囊转发
				return {
				  title: 'ShoYii',
				  path: '/pages/index/index'
				}
			}
		},
		// #endif
		// 双击tabbar
		onTabItemTap(e) { // tab 点击时执行，此处直接接收单击事件 
			console.log(e) 
			if (this.tabClick) { 
				// 200ms 内再次点击 数据处理
				this.reflesh()
			} 
			this.tabClick = true 
			dbclickTimer=setTimeout(() => { 
				this.tabClick = false // 200ms 内没有第二次点击，就当作单击 
			}, 200) 
		},
		onUnload() {
			clearTimeout(dbclickTimer)
			clearTimeout(animationTimer)
			clearTimeout(stopTimer)
		}
	}
</script>
